<!-- 【通知信息】弹窗页面 -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="notice" ref="noticeForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="标题图片" prop="picture">
                <sui-photo type="notice" v-model="notice.picture" :disabled="dialog.disabled"/>
            </el-form-item>
            <el-form-item label="消息标题" prop="title">
                <el-input v-model="notice.title" placeholder="请输入消息标题" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="消息类型" prop="type">
                <el-select v-model="notice.type" placeholder="请选择消息类型" :disabled="dialog.disabled" >
                    <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="发布时间" prop="publishTime">
                <el-date-picker v-model="notice.publishTime" placeholder="请选择发布时间" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
            </el-form-item>
            <el-form-item label="消息内容" prop="content">
                <el-input type="textarea" v-model="notice.content" placeholder="请输入消息内容" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel">取 消</el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('noticeForm')">保 存</el-button>
        </div>
    </el-dialog>
</template>

<script>
import suiPhoto from "@/components/sui-photo/sui-photo";
export default {
    components: {
        suiPhoto,
    },
    name: "noticeForm",
    data() {
      return {
        //弹窗设置
        dialog: {
            title: "",
            open: false,
            disabled: false
        },
        //表单数据
        notice: {
            picture: "",
            title: "",
            type: "",
            publishTime: "",
            content: ""
        },
        //表单校验
        rules: {
            picture: [{required: true, message: '请选择标题图片', trigger: 'change'}],
            title: [{required: true, message: '请输入消息标题', trigger: 'blur'}],
            type: [{required: true, message: '请选择消息类型', trigger: 'change'}],
            publishTime: [{required: true, message: '请选择发布时间', trigger: 'change'}],
            content: [{required: true, message: '请输入消息内容', trigger: 'blur'}]
        }
      }
    },
    computed: {
        //【消息类型】
        typeDict() {
            return this.$store.getters.getDictArray('notice_type');
        },
    },
    methods: {
        //打开弹窗
        open(option) {
            const pageFrom = option.pageFrom;
            const disabled = pageFrom == 'view' ? true : false;
            this.showDialog(option.title, disabled);
            if (pageFrom == 'edit' || pageFrom == 'view') {
                this.getNotice(option.id)
            }
        },
        //根据id获取数据
        getNotice(id) {
            this.$request.doGet("/api/notice/get?id="+id).then(res => {
                if (res.isOk) {
                    this.notice = res.obj;
                }
            });
        },
        //取消弹窗
        cancel: function () {
            this.dialog.open = false
        },
        //提交表单
        submitForm: function (formName) {
            const that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {//表达校验
                    that.$request.doPost('/api/notice/sub', that.notice).then(res => {
                        if (res.isOk) {
                            that.dialog.open = false;
                            that.showSuccess("提交成功");
                            that.$emit('reloadList');
                        }
                    });
                }
            });
        },
        //显示弹窗
        showDialog(title, disabled) {
            this.resetForm(this.$refs.noticeForm);//重置表单
            this.dialog.open = true;//打开弹窗
            this.dialog.title = title;//设置标题
            this.dialog.disabled = disabled;//是否可编辑
        }
    }
}
</script>

<style scoped>
</style>



